<template>
  <div>
    <PageHeader
      :title="$t('header.post_request_title')"
      :subTitle="$t(`header.post_request_sub_title`)"
    />
    <!-- <rent-heading :title="$t('header.post_request_title')" -->
    <div class="posting-form-container">
      <b-row class="align-items-center">
        <b-col lg="4">
          <v-select
            v-model="requestData.destination.city"
            label="location_name"
            :options="propertiesData.rootLocations"
            @input="cityInput"
            :reduce="(location) => location.location_id"
            id="neighborhoodSelect"
            :placeholder="
              $t('form.postingRequest.selectedDestination.input_place_holder')
            "
            class="destination-input"
          ></v-select>
        </b-col>

        <b-col
          lg="4"
          v-if="
            childLocationDistrict.length &&
            requestData.destination.city !== null
          "
        >
          <v-select
            v-model="requestData.destination.district"
            label="location_name"
            :options="childLocationDistrict"
            :reduce="(location) => location.location_id"
            @input="districtInput"
            id="neighborhoodSelect"
            :placeholder="
              $t('form.postingRequest.selectedDestination.input_place_holder')
            "
            class="destination-input"
          ></v-select>
        </b-col>

        <b-col
          lg="4"
          v-if="
            childLocationNeighborhood.length > 0 &&
            requestData.destination.district !== null
          "
        >
          <v-select
            v-model="requestData.destination.neighborhood"
            label="location_name"
            :options="childLocationNeighborhood"
            @input="neighborhoodInput"
            :reduce="(location) => location.location_id"
            id="neighborhoodSelect"
            :placeholder="
              $t('form.postingRequest.selectedDestination.input_place_holder')
            "
            class="destination-input"
          ></v-select>
        </b-col>
      </b-row>
      <b-row class="mt-3">
        <b-col cols="12">
          <div class="accordion posting-accordion" role="tablist">
            <b-card no-body class="mb-3 border-0">
              <b-card-header header-tag="header" class="p-1" role="tab">
                <b-button block v-b-toggle.accordion-1>
                  <h1>
                    {{ $t('form.postingRequest.stayingDetails.heading') }}
                  </h1>
                  <h2 class="light-gray font-size-normal">
                    This is a dummy text and will be replaced with real text
                    later after clients approval, so please don’t read this text
                  </h2>

                  <span class="when-opened">
                    <svg-icon
                      name="arrow-down-blue"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                  <span class="when-closed">
                    <svg-icon
                      name="arrow-down-dark"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                </b-button>
              </b-card-header>
              <b-collapse
                id="accordion-1"
                visible
                accordion="my-accordion"
                role="tabpanel"
              >
                <b-card-body>
                  <b-row>
                    <b-col cols="12" class="mt-4">
                      <h5 class="font-size-normal">
                        {{
                          $t('form.postingRequest.stayingDetails.budget_range')
                        }}
                      </h5>
                      <span class="font-size-smaller light-gray" v-if="duration"
                        >({{ budgetTerm }})</span
                      >
                      <AppInput
                        :placeholder="
                          $t(
                            'form.postingRequest.stayingDetails.budget_placeHolder'
                          )
                        "
                        v-model="requestData.budgetRange"
                        type="number"
                      ></AppInput>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="2">
                      <h5 class="font-size-normal">
                        {{ $t('form.postingRequest.stayingDetails.duration') }}
                      </h5>
                      <CustomCounter
                        id="noOfBathrooms"
                        :step="1"
                        validation-rules="required|min_value:1"
                        v-model="requestData.duration"
                        :min="1"
                        :max="31"
                      />
                    </b-col>
                    <b-col cols="3">
                      <h5 class="font-size-normal">Per</h5>
                      <select v-model="requestData.durationType">
                        <option v-for="option in durationTypes" :value="option">
                          {{ option }}
                        </option>
                      </select>
                    </b-col>

                    <b-col cols="6">
                      <h5 class="font-size-normal">
                        {{
                          $t('form.postingRequest.stayingDetails.starting_date')
                        }}
                      </h5>
                      <b-form-datepicker
                        :date-format-options="dateFormatOptions"
                        id="datepicker"
                        v-model="requestData.startingDate"
                        @input="changeDate"
                        :placeholder="$t('common.date')"
                        today-button
                        reset-button
                        close-button
                        locale="en"
                        aria-describedby="inputcheck-in-feedback"
                      />
                    </b-col>
                  </b-row>
                </b-card-body>
              </b-collapse>
            </b-card>
            <b-card no-body class="mb-3 border-0">
              <b-card-header header-tag="header" class="p-1" role="tab">
                <b-button block v-b-toggle.accordion-2>
                  <h1>{{ $t('form.postingRequest.userTerms.heading') }}</h1>
                  <h2 class="light-gray font-size-normal">
                    This is a dummy text and will be replaced with real text
                    later after clients approval, so please don’t read this text
                  </h2>

                  <span class="when-opened">
                    <svg-icon
                      name="arrow-down-blue"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                  <span class="when-closed">
                    <svg-icon
                      name="arrow-down-dark"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                </b-button>
              </b-card-header>
              <b-collapse
                id="accordion-2"
                visible
                accordion="my-accordion"
                role="tabpanel"
              >
                <b-card-body>
                  <b-row>
                    <b-col cols="12" class="mt-4">
                      <b-form-checkbox-group
                        v-model="requestData.selectedTerms"
                        :options="transformUserConditions"
                        stacked
                      ></b-form-checkbox-group>
                    </b-col>
                  </b-row>
                </b-card-body>
              </b-collapse>
            </b-card>

            <b-card no-body class="mb-3 border-0">
              <b-card-header header-tag="header" class="p-1" role="tab">
                <b-button block v-b-toggle.accordion-3>
                  <h1>
                    {{ $t('form.postingRequest.SelectPreferences.heading') }}
                  </h1>
                  <h2 class="light-gray font-size-normal">
                    This is a dummy text and will be replaced with real text
                    later after clients approval, so please don’t read this text
                  </h2>

                  <span class="when-opened">
                    <svg-icon
                      name="arrow-down-blue"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                  <span class="when-closed">
                    <svg-icon
                      name="arrow-down-dark"
                      height="24"
                      width="24"
                      class="mb-2"
                    ></svg-icon>
                  </span>
                </b-button>
              </b-card-header>
              <b-collapse
                id="accordion-3"
                visible
                accordion="my-accordion"
                role="tabpanel"
              >
                <b-card-body>
                  <b-row class="align-items-center">
                    <b-col cols="2">
                      <h5 class="font-size-semi-large dark-blue">
                        {{ $t('property.furnishing_status') }}
                      </h5>
                    </b-col>
                    <b-col cols="10">
                      <chip-selector
                        :options="transformFurnishTypes"
                        @update-selection="updateFurnishTypes"
                      ></chip-selector>
                    </b-col>
                  </b-row>
                  <hr />
                  <b-row class="align-items-center">
                    <b-col cols="2">
                      <h5 class="font-size-semi-large dark-blue">
                        {{ $t('property.property_types') }}
                      </h5>
                    </b-col>
                    <b-col cols="10">
                      <chip-selector
                        v-bind:options="transformPropertyTypes"
                        @update-selection="updatePropertyTypes"
                      ></chip-selector>
                    </b-col>
                  </b-row>
                  <hr />
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
        </b-col>
      </b-row>
      <b-row class="mt-3">
        <b-col class="d-flex justify-content-end">
          <b-button variant="outline" class="font-weight-bold mr-2">
            {{ $t('form.cancel') }}
          </b-button>
          <b-button @click="confirmData" variant="primary">
            {{ $t('common.next') }}
          </b-button>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect'
import PageHeader from '@/components/desktop/PageHeader'
import RentHeading from '~/components/global/rent-heading'
import calculateMoveOutData from '@/mixins/calculateMoveOutData'

export default {
  name: 'posting-data',
  components: {
    PageHeader,
    rentSelect,
    RentHeading,
    ChipSelector: () => import('~/components/shared/form/chipSelector'),
    CustomCounter: () => import('~/components/shared/form/CustomCounter'),
  },
  mixins: [calculateMoveOutData],

  props: {
    propertiesData: {
      type: Object,
      default: () => {},
    },
    conditionsData: {
      type: Array,
      default: [],
    },
  },
  computed: {
    transformUserConditions() {
      return this.conditionsData.map((condition) => ({
        value: condition.id,
        text: condition[`name_${this.$i18n.locale}`],
      }))
    },

    transformFurnishTypes() {
      return (
        this.propertiesData.furnishType &&
        this.propertiesData.furnishType.map((furnish) => ({
          value: furnish.id,
          label: furnish.text,
        }))
      )
    },
    transformPropertyTypes() {
      return (
        this.propertiesData.propertyType &&
        this.propertiesData.propertyType.map((property) => ({
          value: property.prop_type_id,
          label: property.prop_type_name,
        }))
      )
    },
    duration() {
      return this.$dateFns.differenceInDays(
        new Date(this.moveOutDate),
        new Date(this.requestData.startingDate)
      )
    },

    childLocationNeighborhood() {
      return this.$store.state.PropertyModule.neighborhoodLocation
    },
    childLocationDistrict() {
      return this.$store.state.PropertyModule.districtLocation
    },

    budgetTerm() {
      return this.requestData.durationType === 'years'
        ? this.$t('common.yearly')
        : this.requestData.durationType === 'months'
        ? this.$t('common.monthly')
        : this.$t('common.daily')
    },
  },
  data() {
    return {
      moveOutDate: null,

      requestData: {
        destination: { city: '', district: '', neighborhood: '' },
        furnishType: [],
        propertyType: [],
        duration: 1,
        startingDate: null,
        durationType: 'months',
        selectedTerms: [],
        budgetRange: '',
      },

      durationTypes: ['years', 'months', 'days'],

      dateFormatOptions: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
      },
    }
  },
  watch: {
    requestData: {
      handler(newValue, oldValue) {
        const { durationType, duration, startingDate } = this.requestData
        this.calculateMoveOutData({
          durationType,
          duration,
          startingDate,
        })
      },
      immediate: true,
      deep: true,
    },
    duration: {
      handler(newValue, oldValue) {
        if (!newValue) return

        let term = newValue > 30 ? 1 : 0
        this.$emit('getConditions', term)
      },
      immediate: true,
    },
  },
  methods: {
    cityInput(cityId) {
      if (!cityId) return

      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: cityId,
        type: 'district',
      })
    },
    districtInput(districtId) {
      if (!districtId) return
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: districtId,
        type: 'neighborhood',
      })
    },
    updatePropertyTypes(values) {
      this.requestData.propertyType = values
    },
    updateFurnishTypes(values) {
      this.requestData.furnishType = values
    },
    changeDate(value) {
      this.requestData.startingDate = value
    },
    confirmData() {
      const {
        destination,
        furnishType,
        propertyType,
        duration,
        startingDate,
        durationType,
        budgetRange,
      } = this.requestData

      if (
        !destination?.city ||
        !furnishType.length ||
        !propertyType.length ||
        !duration ||
        !startingDate ||
        !durationType ||
        !budgetRange
      ) {
        let body = this.$t('form.checkListFirst')
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        })
        return
      }
      let furnishTypes = this.propertiesData.furnishType.reduce(
        (accumulator, currentValue) => {
          if (furnishType.some((f) => f === currentValue.id)) {
            accumulator.push(currentValue.text)
          }
          return accumulator
        },
        []
      )
      this.requestData = { ...this.requestData, furnishType: furnishTypes }
      this.$emit('confirm', this.requestData)
    },
  },
}
</script>
<style lang="scss">
.posting-form-container {
  background: #ffffff;
  /* Shadow effect */

  box-shadow: 0px 4px 8px rgba(39, 39, 39, 0.08);
  border-radius: 8px;
  max-width: calc(100% - 80px);
  /* margin: auto; */
  margin: -40px auto 0;

  padding: 48px;
}

.accordion.posting-accordion {
  button {
    position: relative;
    border: none;
    background: linear-gradient(
        0deg,
        rgba(38, 45, 61, 0.04),
        rgba(38, 45, 61, 0.04)
      ),
      #ffffff;
    &:focus {
      outline: none;
      box-shadow: none;
    }
    h1 {
      font-weight: 700;
      font-size: 24px;
      line-height: 32px;

      color: $gray;
    }
  }
}
.posting-accordion .collapsed > .when-opened,
:not(.collapsed) > .when-closed {
  display: none;
}

.posting-accordion .when-closed,
.posting-accordion .when-opened {
  position: absolute;
  right: 0;
  left: auto;
  top: 50%;
}
[dir='rtl'] .posting-accordion .when-closed,
[dir='rtl'] .posting-accordion .when-opened {
  left: 5px;
  right: auto;
}

.posting-accordion .custom-control.custom-checkbox {
  width: 100%;

  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  background: linear-gradient(
      0deg,
      rgba(38, 45, 61, 0.04),
      rgba(38, 45, 61, 0.04)
    ),
    #ffffff;

  span: {
    color: #0f256e !important;
    font-weight: bold;
    margin-left: 1.5rem;
    margin-bottom: 0 !important;
  }
}

.posting-accordion .custom-control-label span {
  color: #0f256e !important;
  font-weight: bold;
  margin-left: 1.5rem;
  margin-bottom: 0 !important;
}

[dir='rtl'] .posting-accordion .custom-control-label span {
  margin-left: unset;
  margin-right: 1.5rem;
}

[dir='rtl']
  .posting-accordion
  .custom-control.custom-checkbox
  .custom-control-label:before,
[dir='rtl']
  .posting-accordion
  .custom-control.custom-checkbox
  .custom-control-label:after {
  right: 0;
}

.posting-accordion .custom-control-label::before {
  left: 0;
}

.posting-accordion .chips-container .chip.selected .chip-label {
  color: white !important;
}
.posting-accordion .vs__dropdown-menu {
  z-index: 10000000;
}

.posting-accordion .dropdown-menu {
  z-index: 10030 !important;
}

.posting-accordion .counter-input-contianer {
  padding: 2px 5px !important;
}
</style>
