<template>
  <b-row class="align-items-center justify-content-center" >
    <b-col lg="6" class="order-2 order-lg-1">
      <h2
        class="text-capitalize-2 font-weight-bold font-size-large text-center">
        {{ $t('form.postingRequest.selectedDestination.heading') }}
      </h2>
      <b-row class="justify-content-center">
        <b-col lg="6" class="mb-4">
          <rent-select
            :options="propertiesData.rootLocations"
            id="citySelect"
            @input="cityInput"
            title="Select city"
            :iconName="require(`@/assets/svg-sprite/svg/city.svg`)"
            :placeholder="$t('form.select_city')"
            label="location_name"
            value="location_id"
            class="destination-input"
            :defaultValue="cityDefault">s
          </rent-select>

          <rent-select
            :dir="dir"
            v-if="childLocationDistrict.length > 0 && destination.city !== null"
            label="location_name"
            :options="childLocationDistrict"
            @input="districtInput"
            title="Select district"
            :iconName="require(`@/assets/svg-sprite/svg/district.svg`)"
            :placeholder="$t('form.select_district')"
            :defaultValue="districtDefault">
          </rent-select>

          <rent-select
            :dir="dir"
            label="location_name"
            :options="childLocationNeighborhood"
            @input="neighborhoodInput"
            v-if="
              childLocationNeighborhood.length > 0 &&
              destination.district !== null
            "
            title="Select neighbourhood"
            :iconName="require(`@/assets/svg-sprite/svg/neighborhood.svg`)"
            :placeholder="$t('form.choose_neighborhood')"
            :defaultValue="neighborhoodDefault">
          </rent-select>
        </b-col>
        <b-col cols="12">
          <b-row class="step-next-container mx-auto">
            <b-col cols="5">
              <div>
                <b-button
                  @click="() => {$router.push(this.localePath('/requests/post/property-category'))}"
                  variant="primary"
                  class="back-btn">
                  <span>{{ $t('common.back') }}</span>
                </b-button>
              </div>
            </b-col>
            <b-col cols="7">
              <div>
                <b-button
                  @click="handleNextClick"
                  variant="primary"
                  class="next-btn"
                  :disabled="!destination.city">
                  <span>{{ $t('common.next') }}</span>
                </b-button>
              </div>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-col>
  </b-row>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect';

export default {
  components: {
    rentSelect,
  },
  name: 'SelectDestination',
  props: {
    propertiesData: Object,
  },

  data() {
    return {
      destination: {
        city: '',
        district: '',
        neighborhood: '',
      },
    };
  },

  computed: {
    childLocationNeighborhood() {
      return this.$store.state.PropertyModule.neighborhoodLocation;
    },
    childLocationDistrict() {
      return this.$store.state.PropertyModule.districtLocation;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    cityDefault() {
      return (
        this.propertiesData?.rootLocations?.find(
          (city) => city.location_id === this.destination?.city
        ) || ''
      );
    },
    districtDefault() {
      return (
        this.childLocationDistrict?.find(
          (district) => district.location_id === this.destination?.district
        ) || ''
      );
    },
    neighborhoodDefault() {
      if (!this.districtDefault) return '';
      return (
        this.childLocationNeighborhood?.find(
          (neighborhood) =>
            neighborhood.location_id === this.destination?.neighborhood
        ) || ''
      );
    },
  },

  methods: {
    handleNextClick() {
      const { city } = this.destination;
      if (!city) {
        let body = this.$t('form.postingRequest.selectedDestination.fillCity');
        this.$bvToast.toast(body, {
          title: this.$t('form.postingRequest.selectedDestination.fillCity'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }
      this.$emit('nextStep', this.destination);
    },

    cityInput(city) {
      if (!city.location_id) return;

      this.destination.city = city.location_id;

      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: city.location_id,
        type: 'district',
      });
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: '',
        type: 'neighborhood',
      });
    },

    districtInput(district) {
      if (!district?.location_id) return;

      this.destination.district = district.location_id;
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: district.location_id,
        type: 'neighborhood',
      });
    },
    neighborhoodInput(neighborhood) {
      this.destination.neighborhood = neighborhood.location_id;
    },
  },
  mounted() {
    let request = localStorage.getItem('request') && JSON.parse(localStorage.getItem('request'));
    let propertyCategory = request && request.propertyCategory;
  
    if(!propertyCategory){
      this.$router.push(this.localePath('/requests/post/property-category'))
    }

    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    const currentDestination = (JSON.parse(localStorage.getItem('request')) && JSON.parse(localStorage.getItem('request')).selectedDestination != 'undefined') ? JSON.parse(localStorage.getItem('request')).selectedDestination : {};


    if (!!currentDestination) this.destination = currentDestination;
  },
};
</script>
<style lang="scss" scoped>
.rent-select {
  margin-bottom: 20px;
}
.step {
  small {
    margin: -10px 0 10px;
    display: block;
    font-size: 12px;
    color: $secondaryTextColor;
  }
  margin-bottom: 50px;
}
h2 {
  font-weight: 800;
  margin-bottom: 24px;
}
h6 {
  font-size: 16px;
  font-weight: 800;
  margin-bottom: 16px;
  text-transform: capitalize;
}
::v-deep() label {
  font-size: 14px;
}
.star-rate {
  width: 25px;
}
::v-deep() .custome-input .custom-control {
  padding: 0;
  margin: 0;
}
::v-deep() input,
::v-deep() textarea {
  text-align: start;
  unicode-bidi: plaintext;
}
textarea::placeholder,
input::placeholder {
  font-size: 12px;
}
.city-img {
  width: 48px;
  height: 48px;
  border-radius: 8px;
}

</style>