<template>
  <div class="add-property py-2">
    <postingRequestForm
      @changeActiveStep="changeActiveStep"
    />
  </div>
</template>

<script>
import postingRequestForm from '@/components/postingRequestForm/FormContainer'
import postingRequestFormDesktop from '@/components/desktop/postingRequestForm/FormContainer'
import PageHeader from '@/components/desktop/PageHeader'
import RentHeading from '~/components/global/rent-heading'

export default {
  data() {
    return {
      activeStep: 1,
    }
  },
  components: {
    postingRequestForm,
    postingRequestFormDesktop,
    PageHeader,
    RentHeading,
  },
  computed: {
    isRTL() {
      return this.$store.getters['CoreModule/isRTL']
    },
  },

  methods: {
    changeActiveStep(value) {
      this.activeStep = this.activeStep + value
    },
  },
}
</script>
<style lang="scss" scoped>
.is-mobile-device{
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}
.add-property {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  .property-list-container {
    position: sticky;
    background: #fff;
    z-index: 9;
    overflow-x: scroll;
    h6 {
      margin-bottom: 0;
    }
    &::-webkit-scrollbar {
      height: 10px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
    }
    &::-webkit-scrollbar-thumb {
      background: #fff;
    }
    &::-webkit-scrollbar-thumb:hover {
      background: $grayLightColor;
    }
  }
  .add-property-list {
    text-align: center;
    padding: 0;
    margin: 0;
    list-style: none;
    padding: 30px 0;
    border-top: 1px solid $grayLightColor;
    width: max-content;
    min-width: 100%;
    white-space: nowrap;
    li {
      display: inline-block;
      color: $mainTextColor;
      font-size: 14px;
      padding: 0 55px 0 25px;
      position: relative;
      &:before,
      &:after {
        position: absolute;
        content: '';
      }
      &::before {
        background: url(@/assets/images/add-property/next.png);
        background-size: 21px;
        background-repeat: no-repeat;
        width: 21px;
        height: 21px;
        left: 0;
      }
      &::after {
        height: 3px;
        width: 46px;
        background: $grayBackgroundColor;
        top: 40%;
        right: 0;
      }
      &:last-of-type {
        padding-right: 5px;
        &::after {
          display: none;
        }
      }
    }
    li.previous {
      &::before {
        background: url(@/assets/images/add-property/previous.png);
        background-size: 21px;
        background-repeat: no-repeat;
      }
      &::after {
        background: $mainColor;
      }
    }
    li.current {
      &::before {
        background: url(@/assets/images/add-property/current.png);
        background-size: 21px;
        background-repeat: no-repeat;
      }
    }
  }
}
[dir='rtl'] .add-property {
  .add-property-list {
    li {
      padding: 0 25px 0 55px;
      &::before {
        left: auto;
        right: 0;
      }
      &::after {
        left: 0;
        right: auto;
      }
      &:last-of-type {
        padding-left: 5px;
        padding-right: 25px;
      }
    }
    li.previous {
      &::before {
        background: url(@/assets/images/add-property/previous.png);
        background-size: 21px;
        background-repeat: no-repeat;
      }
    }
    li.current {
      &::before {
        background: url(@/assets/images/add-property/current.png);
        background-size: 21px;
        background-repeat: no-repeat;
      }
    }
  }
}
</style>
