<template>
  <div class="row">
    <b-col cols="12">
      <h1 class="second-step__title font-weight-semi font-size-large">
        {{ $t('form.postingRequest.SelectPreferences.heading') }}
      </h1>
    </b-col>

    <b-col cols="12">
      <h5 class="font-size-semi-large dark-blue">
        {{ $t('property.furnishing_status') }}
      </h5>
      <chip-selector
        :options="transformFurnishTypes"
        @update-selection="updateFurnishTypes"
      ></chip-selector>
    </b-col>
    <b-col cols="12" class="mt-4">
      <h5 class="font-size-normal dark-blue">
        {{ $t('form.postingRequest.SelectPreferences.minimum_rooms') }}
      </h5>
      <CustomCounter
        id="minimumRooms"
        :step="1"
        validation-rules="required|min_value:1"
        v-model="selectPreferences.miniumRooms"
        :min="1"
        :max="10"
      />
    </b-col>

    <b-col cols="12" class="mt-4">
      <h5 class="font-size-semi-large dark-blue">
        {{ $t('property.property_types') }}
      </h5>
      <chip-selector
        v-bind:options="transformPropertyTypes"
        @update-selection="updatePropertyTypes"
      ></chip-selector>
    </b-col>
    <b-col cols="12">
      <div class="step-next-container">
        <b-button @click="handleNextClick" variant="primary" class="next-btn">
          <svg-icon name="next" :fill="'#FFFFF'" />
        </b-button>
      </div>
    </b-col>
  </div>
</template>

<script>
export default {
  name: 'SelectPreferences',
  props: {
    propertiesData: { type: Object, default: () => {} },
  },
  components: {
    ChipSelector: () => import('~/components/shared/form/chipSelector'),
    CustomCounter: () => import('~/components/shared/form/CustomCounter'),
  },
  data() {
    return {
      selectPreferences: {
        miniumRooms: 1,
        furnishType: null,
        propertyType: null,
      },
    }
  },
  computed: {
    transformFurnishTypes() {
      return (
        this.propertiesData.furnishType &&
        this.propertiesData.furnishType.map((furnish) => ({
          value: furnish.id,
          label: furnish.text,
        }))
      )
    },
    transformPropertyTypes() {
      return (
        this.propertiesData.propertyType &&
        this.propertiesData.propertyType.map((property) => ({
          value: property.prop_type_id,
          label: property.prop_type_name,
        }))
      )
    },
  },

  methods: {
    updatePropertyTypes(values) {
      this.selectPreferences.propertyType = values
    },
    updateFurnishTypes(values) {
      this.selectPreferences.furnishType = values
    },
    handleNextClick() {
      let { furnishType, miniumRooms, propertyType } = this.selectPreferences
      if (!furnishType.length && !miniumRooms && !propertyType.length) {
        let body = this.$t('form.checkListFirst')
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        })
        return
      }
      let furnishTypes = this.propertiesData.furnishType.reduce(
        (accumulator, currentValue) => {
          if (furnishType.some((f) => f === currentValue.id)) {
            accumulator.push(currentValue.text)
          }
          return accumulator
        },
        []
      )

      this.selectPreferences = {
        ...this.selectPreferences,
        furnishType: furnishTypes,
      }
      this.$emit('nextStep', this.selectPreferences)
    },
  },
}
</script>

<style lang="scss" scoped></style>
