<template>
  <div>
    <div class="posting-header">
      <b-btn
        @click="
          $router.push({
            path: localePath('/'),
          })
        "
        class="btn btn-sm btn-outline-primary bg-white m-0 border-0 go-home"
      >
        <span class="x"> > Home</span>
      </b-btn>
    </div>
    <!-- &#x2192; -->
    <div class="p-3 post-form-container">
      <div class="step" v-show="step == 0">
        <select-destination
          @nextStep="handleDestinationClick"
          :propertiesData="propertiesData"
        />
      </div>

      <!-- second step -->
      <div class="step" v-show="step == 1">
        <staying-details @nextStep="handleStayingDetailsClick" />
      </div>
      <!-- third step -->
      <div class="step" v-if="step == 2">
        <user-terms
          @nextStep="handleSelectPreferenceClick"
          :userTerms="postingRequest.userTerms"
          :conditionsData="userConditions"
          :propertiesData="propertiesData"
        />
      </div>
      <!-- fifth step -->
      <div class="step" v-if="step === 3">
        <posting-preview
          :postingRequest="postingRequest"
          :conditionsData="userConditions"
          :propertiesData="propertiesData"
          @submitRequest="handleSubmitClick"
        />
      </div>
    </div>
  </div>
</template>

<script>
import SelectDestination from './SelectDestination'
import StayingDetails from './StayingDetails'
import UserTerms from './UserTerms'
import SelectPreferences from './SelectPreferences'
import PostingPreview from './PostingPreview'
import {
  fetchPropertiesData,
  fetchUserTerms,
  postingRequest,
} from '@/api/properties'
import { createNamespacedHelpers } from 'vuex'
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule')
const { mapActions } = createNamespacedHelpers('PropertyModule')

const { mapState } = createNamespacedHelpers('UserModule')
const { mapGetters } = createNamespacedHelpers('CoreModule')

export default {
  components: {
    SelectDestination,
    StayingDetails,
    UserTerms,
    SelectPreferences,
    PostingPreview,
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isRTL']),
  },
  data() {
    return {
      step: 0,
      propertiesData: {},
      userConditions: [],
      postingRequest: {
        selectedDestination: null,
        stayingDetails: null,
        userTerms: {
          selectedTerms: [],
        },
        selectPreferences: {},
      },
    }
  },

  fetch() {
    this.getPropertiesData()
  },

  methods: {
    ...mapAuthActions(['showAuthModal']),
    ...mapActions(['postRequest']),

    nextStep() {
      this.step++
      this.$emit('changestep', 1)
    },
    async getPropertiesData() {
      try {
        const response = await fetchPropertiesData()

        this.propertiesData = response.Data
      } catch (error) {
        console.log(error)
      }
    },

    handleDestinationClick(value) {
      this.postingRequest.selectedDestination = value
      this.nextStep()
    },

    async handleStayingDetailsClick(data) {
      this.postingRequest.stayingDetails = { ...data.stayingDetails }
      const response = await fetchUserTerms(data.term)

      this.userConditions = response.Data.conditions
      this.nextStep()
    },
    handleSelectPreferenceClick(data) {
      this.postingRequest.selectPreferences = { ...data }
      this.step++
      this.$emit('changestep', 1)
    },
    transformUserTerms() {
      this.userConditions = this.userConditions.filter((condition) => {
        if (
          this.postingRequest.selectPreferences.userTerms.selectedTerms.indexOf(condition.id) >= 0
        ) {
          return {
            condition_id: condition.id,
          }
        }
      });
      return this.userConditions.map((condition) => {
        return {
          condition_id: condition.id,
        }
      })
    },

    async handleSubmitClick(moveOutDate) {
      const { selectedDestination, stayingDetails, selectPreferences } =
        this.postingRequest

      let stayingDuration = this.$dateFns.differenceInDays(
        new Date(moveOutDate),
        new Date(stayingDetails.startingDate)
      )
      const { city, district, neighborhood } = selectedDestination
      let locationId = neighborhood ? neighborhood : district ? district : city
      const payload = {
        name: null,
        type: 1,
        term: stayingDuration > 30 ? 1 : 0,
        move_in_date: stayingDetails.startingDate,
        move_out_date: this.$dateFns.format(
          new Date(moveOutDate),
          'yyyy-MM-dd'
        ),
        budget: stayingDetails.budgetRange,
        num_children: null,
        num_of_bed_rooms: selectPreferences.miniumRooms,
        num_of_bath_rooms: null,
        location_id: locationId,
        property_type_ids: selectPreferences.propertyType,
        is_date_flexible: 1,
        notes: null,
        conditions: this.transformUserTerms(),
        furnishing_status: selectPreferences.furnishType,
      }

      if (!this.user) {
        this.$store.dispatch(
          'AuthModule/showAuthModal',
          {
            callbackAction: {
              name: 'PropertyModule/addProperty',
              payload,
            },
          },
          {
            root: true,
          }
        )
      }

      this.postRequest(payload).finally((response) => {
      });

    },
  },
}
</script>

<style lang="scss" scoped>
::v-deep() .icon-container {
  background: #ffffff;
  border-radius: 16px;
  padding: 12px;
  width: 56px;
  height: 56px;
}
.step-heading {
  height: 56px;
  padding: 10px 24px;
  background-color: white;
  .btn.go-home {
    width: 36px;
    height: 36px;
    background-color: #f7f9fa !important;
    border-radius: 100px;
    .x {
      color: black;
      font-weight: bolder;
      font-size: x-large;
    }
  }
}

::v-deep() .section-white {
  background-color: white;
  margin: 8px 0;
  padding: 10px;

  @media only screen and (min-width: 1024px) {
    margin: 0;
    margin-top: 0 !important;
    background-color: transparent !important;
    &:not(:last-child) {
      border-bottom: 1px solid #f2f4f5;
    }
  }
}
.post-form-container {
  background: #f7f9fa;
  display: block;
  position: relative;
  top: 0;
  width: 100vw;
  margin: 0;
  min-height: 100vh;
}
.step {
  .btn-outline-primary,
  .btn-primary {
    height: 40px;
    min-width: 100px;
    line-height: 0;
    font-size: 16px;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 800;
    .arrow {
      font-size: 18px;
    }
  }
  .btn-primary {
    float: right;
  }
  ::v-deep() .btn-outline-primary {
    float: left;
    border-color: transparent;
  }
  margin-bottom: 50px;
  ::v-deep() .step-content {
    @media only screen and (min-width: 1024px) {
      border-radius: 20px;
      background-color: white;
      padding: 30px !important;
    }
  }
}
[dir='rtl'] .step {
  .btn-primary {
    float: left;
  }
  .btn-outline-primary {
    float: right;
  }
}
</style>
<style lang="scss">
.step-next-container {
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 999;
  left: 0;
  background-color: white;
  height: 96px;
  padding: 16px 24px;
  .btn {
    padding: 14px;
    height: 48px;
    background: #0078ff;
    border-radius: 8px;
    width: 100%;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: #ffffff;
    svg path {
      fill: white;
    }
    &.back-btn {
      background-color: #f7f9fa;
      color: black;
      border: none;
    }
  }
}
.back-reverse {
  transform: rotate(180deg);
}

@media only screen and (min-width: 1024px) {
  .step-next-container {
    position: relative;
    background-color: transparent;
    z-index: 1;
    max-width: 500px;
    margin: auto;
    .btn.back-btn {
      background-color: white;
    }
  }
}
</style>
